@import "../../scss/abstracts";
.about {
    padding: 8vh 6vw;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include respond(large-screen) {
        flex-direction: column;
    }
    &__article {
        padding-right: 3rem;
        header {
            display: inline-block;
            color: $color-primary;
        }
        h1 {
            font-size: $font-larger-size;
            position: relative;
            z-index: 1000;
            @include respond(small-screen) {
                font-size: $font-large-size;
            }
            &:hover .icon {
                animation: wave 0.9s ease infinite;
                transform-origin: bottom right !important;
            }
        }
        .icon {
            padding-left: 3px;
            display: inline-block;
            animation: wave 0.5s ease alternate-reverse;
            transform-origin: bottom right !important;
        }
        p {
            font-size: $font-small-size;
            line-height: 1.8;
            max-width: 55ch;
            margin-bottom: 2rem;
            margin: 3.5rem 0;
        }
        button {
            margin: 0;
        }
    }
    &__skills {
        color: $color-primary;
        padding-right: 5vw;
        h1 {
            font-size: $font-medium-size;
            margin: 3rem 0;
            line-height: 1;
            text-decoration: underline;
            text-decoration-style: wavy !important;
            text-decoration-color: rgba($color-secondary, 0.8);
        }
        &__list {
            font-size: $font-small-size;
            line-height: 1.8;
            display: flex;
            @include respond(large-screen) {
                justify-content: space-between;
            }
            @include respond(small-screen) {
                display: block;
            }
            &__skills,
            &__tools {
                flex-grow: 1;
            }
            li {
                list-style: none;
                margin: 1px 0;
                img {
                    margin-right: 0.5rem;
                    width: 1.9rem;
                }
            }
        }
    }
}

@keyframes wave {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(9deg);
    }
    20% {
        transform: rotate(18deg);
    }
    30% {
        transform: rotate(24deg);
    }
    40% {
        transform: rotate(36deg);
    }
    50% {
        transform: rotate(45deg);
    }
    60% {
        transform: rotate(36deg);
    }
    70% {
        transform: rotate(24deg);
    }
    80% {
        transform: rotate(18deg);
    }
    90% {
        transform: rotate(9deg);
    }
    100% {
        transform: rotate(0deg);
    }
}