$color-primary: #383838;
$color-secondary: #8c52ff;
$color-tertiary: #ffffff;
//
$font-larger-size:4.3rem;
$font-large-size:3.5rem;
$font-medium-size:3rem;
$font-small-size:2rem;
$font-smaller-size:1.6rem;
//
$font-bolder: 600;
$font-bold: 500;
$font-regular: 400;
$font-light: 300;
$font-lighter: 200;
.gradient {
    background: linear-gradient(to bottom left, $color-secondary, #2ebf91);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

@mixin respond($breakpoint) {
    @if $breakpoint==extra-small-screen {
        @media only screen and (max-width: 21.87em) {
            @content;
        }
    }
    @if $breakpoint==smaller-screen {
        @media only screen and (max-width: 36.87em) {
            @content;
        }
    }
    @if $breakpoint==small-screen {
        @media only screen and (max-width: 54.37em) {
            @content;
        }
    }
    @if $breakpoint==mid-screen {
        @media only screen and (max-width: 62.5em) {
            @content;
        }
    }
    @if $breakpoint==large-screen {
        @media only screen and (max-width: 75em) {
            @content;
        }
    }
    @if $breakpoint==larger-screen {
        @media only screen and (max-width: 81.25em) {
            @content;
        }
    }
    @if $breakpoint==extra-larg-screen {
        @media only screen and (min-width: 125em) {
            @content;
        }
    }
}

@mixin typingAnimation {
    animation: typing 3s steps(25, end) forwards;
}

@keyframes typing {
    from {
        width: 1rem;
        visibility: hidden;
    }
    to {
        width: 94vw;
        visibility: visible;
        z-index: -100;
    }
}

.smaller-size {
    width: 2.7rem;
    height: 2.7rem;
}